import { openDB } from 'idb';

const dbPromise = openDB('Agenda', 1, {
  upgrade(db) {
    if (
      !db.objectStoreNames.contains('owner') &&
      !db.objectStoreNames.contains('agenda')
    ) {
      const owner = db.createObjectStore('owner', {
        keyPath: 'id',
        autoIncrement: true
      });
      const agenda = db.createObjectStore('agenda', {
        keyPath: 'id',
        autoIncrement: true
      });
      agenda.createIndex('timestamp', 'timestamp');
      owner.createIndex('username', 'username');
    }
  }
});

export default {
  dbPromise
};
