import Vue from 'vue';
import VueRouter from 'vue-router';
import idb from '../api/indexeddb.js';

Vue.use(VueRouter);
const routes = [
  {
    path: '/',
    name: 'Login',
    component: () =>
      import(/* webpackChunckName: "login"*/ '../views/Login.vue'),
    beforeEnter(to, from, next) {
      return idb.dbPromise.then(async db => {
        let owner = await db.getAll('owner');
        if (owner.length > 0) {
          await next({
            name: 'Main'
          });
        } else {
          await next();
        }
      });
    }
  },
  {
    path: '/main',
    name: 'Main',
    component: () => import(/* webpackChunckName: "main"*/ '../views/Main.vue')
  }
];

const router = new VueRouter({
  mode: 'history',
  routes
});

export default router;
