import Vue from 'vue';
import Vuex from 'vuex';
import idb from '../api/indexeddb.js';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    agenda: {},
    owner: {},
    agendaList: [],
    loginErr: false,
    mainErr: false
  },
  mutations: {
    GET_EVENT(state, data) {
      state.agendaList = data;
    },
    SET_OWNER(state, username) {
      state.owner = Object.assign(username);
    },
    GET_OWNER(state, user) {
      state.owner = Object.assign(user[0]);
    },
    SET_ERR(state, logErr, mainErr) {
      if (logErr) {
        state.loginErr = logErr;
      } else if (mainErr) {
        state.mainErr = mainErr;
      }
    }
  },
  actions: {
    async addEvent({ commit, dispatch }, event) {
      try {
        await idb.dbPromise
          .then(async db => {
            let tx = await db.transaction('agenda', 'readwrite');
            let store = tx.objectStore('agenda');
            await store.put({
              event: event.task,
              timestamp: new Date(event.timestamp),
              done: event.done,
              background: event.bg
            });
            await tx.done;
          })
          .then(() => {
            dispatch('getEvents');
          });
      } catch {
        let mainErr = await true;
        await commit('SET_ERR', mainErr);
      }
    },
    async getEvents({ commit }) {
      await idb.dbPromise.then(async db => {
        let data = await db.getAll('agenda');
        await commit('GET_EVENT', data);
      });
    },
    async deleteEvent({ dispatch }, id) {
      await idb.dbPromise
        .then(async db => {
          let tx = db.transaction('agenda', 'readwrite');
          await tx.objectStore('agenda').delete(id);
          await tx.done;
        })
        .then(() => dispatch('getEvents'));
    },
    async doneEvent({ dispatch }, event) {
      await idb.dbPromise
        .then(async db => {
          let tx = db.transaction('agenda', 'readwrite');
          let store = tx.objectStore('agenda');
          await store.put({
            event: event.task,
            done: event.done,
            timestamp: new Date(event.timestamp),
            background: event.bg,
            id: event.id
          });
        })
        .then(() => dispatch('getEvents'));
    },
    async setOwner({ state, commit }, username) {
      await commit('SET_OWNER', username);
      try {
        await idb.dbPromise
          .then(async db => {
            let tx = await db.transaction('owner', 'readwrite');
            let store = await tx.objectStore('owner');
            await store.put(state.owner);
            await tx.done;
          })
          .then(async () => {
            state.owner = await {};
          });
      } catch {
        let logErr = await true;
        await commit('SET_ERR', logErr);
      }
    },
    async getOwner({ commit }) {
      await idb.dbPromise.then(async db => {
        let user = await db.getAllFromIndex('owner', 'username');
        await commit('GET_OWNER', user);
      });
    }
  }
});
